import axios from "axios";
import React from "react";
import Movie from "./Movie"
import "./App.css"


class App extends React.Component{
  state = {
    isLoading : true,
    movies : []
  };

  getMovies = async () => {
    const  {
      data:{
        data: {
          movies
        }
      }
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json")
    
    this.setState({isLoading:false, movies: movies})
  }

  componentDidMount(){
      this.getMovies();
  }

  render(){
    const {isLoading, movies} = this.state;
    return(
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text"> Loading... </span>
          </div>
        ) : (
          <div className="movies"> 
            {movies.map(movie=>(
              <Movie key={movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image} genres={movie.genres}/>
            ))}
          </div>
        )}
      </section>
    )
       //   <Movie key={item.id} id={item.id} year={item.year} title={item.title} summary={item.summary} poster={item.medium_cover_image}/>
  }
}

export default App;
